@keyframes slide{
    from{
      top : -100%;
      opacity: 0;
    }
    to{
      top : 0px;
      opacity: 1;
    }
  }

  .select{
    position: absolute;
    top: 65%;
    left: 25%;
    width: 50%;
    height: 50px;
    margin: 0px;
    opacity: 0.8;
    font-size: 25px;
    text-align: center;
  }
  select:focus{
    border: 1px solid #9B51E0;
    box-sizing: border-box;
    border-radius: 10px;
    outline: 3px solid #F8E4FF;
    border-radius: 10px;
    opacity: 0.8;
  }
  option {
    padding: 4px;
    font-size: 19px;
